* {
  box-sizing: border-box;
  max-width: 880px;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  border: .8rem solid #000;
  width: 100%;
  max-width: 880px;
  height: auto;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
  line-height: 1;
  box-shadow: inset 0 2px 10px #000;
}

html {
  scroll-behavior: smooth;
  margin: 0 auto;
  font-size: 62.5%;
}

.headerImg {
  text-shadow: .7px .7px 1px #fff, -.7px -.7px 1px #000, 2px 2px 10px #fff;
  color: #00ffffeb;
  background: linear-gradient(145deg, #10ffff, #0ed8e6);
  justify-content: space-evenly;
  display: flex;
  box-shadow: inset -1px -2px 10px #000, inset 1px 2px 10px #fff;
}

.headerImg img {
  background: linear-gradient(145deg, #10ffff, #0ed8e6);
  border-radius: 115px;
  width: 300px;
  max-width: 100%;
  margin: 2rem;
  padding: 2rem;
  box-shadow: 10px 10px 10px #0bb4bf, -10px -10px 10px #13ffff;
}

.navSkills {
  flex-direction: column;
  justify-content: center;
  display: flex;
}

nav {
  border: 1rem solid #0ff;
  border-radius: 5px;
  justify-content: flex-end;
  align-items: center;
  width: auto;
  display: flex;
  position: fixed;
  top: .9rem;
  box-shadow: .12px .24px 4px #000, -.12px -.24px 4px #fff;
}

nav a {
  text-shadow: .5px .5px 1.1px #000, -.9px -.9px 1.1px #f9f9f9;
  color: #0ff999;
  background: #00ff7f;
  border-radius: 5px;
  padding: 1rem;
  font-size: 4rem;
  transition: text-shadow .125s;
  box-shadow: .12px .24px 4px #000, -.12px -.24px 4px #fff;
}

nav a:hover, nav a:active {
  text-shadow: .7px .7px 1px #fff, -.7px -.7px 1px #000, 2px 2px 10px #fff;
  box-shadow: inset 0 0 4px 1px #000, inset 0 6px 7px #008b8b;
}

section#skills {
  font-variant: unicase;
  text-shadow: -.5px -.5px 1.1px #000, .9px .9px 1.1px #f9f9f9;
  width: 100%;
  padding: 2rem;
  font-size: 3rem;
  font-weight: 600;
}

h2 {
  font-size: 3rem;
}

#skills > p {
  color: #00ced1;
}

main {
  display: block;
}

#about {
  flex-direction: column;
  display: flex;
}

#about h2 {
  font-variant: unicase;
  text-shadow: 1.12px 1.48px 4px #714505, -1.12px -2.48px 4px #f1d196;
  color: #ff8c00;
  padding-left: 30px;
  font-size: 10rem;
  box-shadow: inset -222px 222px 220px 218px #ff9800;
}

.introduction {
  text-shadow: -.5px -.5px 1.1px #000, .9px .9px 1.1px #f9f9f9;
  font-variant: titling-caps;
  width: 100%;
  margin: 0 auto;
  padding: 2rem;
  font-size: 2rem;
  font-weight: 600;
}

.introduction > p {
  border-bottom: 1rem solid #0ff;
  margin: 1rem 4rem 1rem 1rem;
  padding: .1rem;
  line-height: 1.5;
}

section#projects {
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 100%;
  padding-top: 2rem;
  display: flex;
}

.card {
  color: #00ffffeb;
  text-shadow: 1px 1px .1px #000, -1px -1px .1px #fff;
  background: #0ff;
  border: .5rem groove #0ff999;
  border-radius: 1%;
  flex-direction: column;
  justify-content: space-around;
  width: auto;
  max-width: 64%;
  height: auto;
  max-height: 550px;
  margin: 1rem 0;
  font-weight: 700;
  display: flex;
}

.card img {
  box-shadow: none;
  text-align: center;
  border-bottom: .8rem groove #fff;
  border-radius: 5px;
  width: -webkit-fill-available;
  height: 400px;
}

.card h3 {
  text-align: center;
  border-bottom: .5rem groove #0ff999;
  margin: 0;
  padding: .5rem;
  font-size: 5rem;
  box-shadow: 0 0 #000;
}

.projectInfoBlock {
  text-align: center;
  height: 5rem;
  display: flex;
}

.projectInfoBlock a {
  color: #faebd7;
  text-shadow: 2px 0 .1px #000;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  width: -webkit-fill-available;
  margin-top: 1rem;
  padding: .5rem;
  font-size: 1.6rem;
  font-weight: bolder;
  text-decoration: none;
  display: flex;
}

.projectInfoBlock a:first-child {
  text-shadow: .5px .5px 1.1px #000, -.9px -.9px 1.1px #f9f9f9;
  color: #0ff;
  border-left: 1px solid #09a565;
  border-right: 1.5px solid #0ff999;
  font-weight: 500;
  transition: text-shadow .25s, transform 1s;
  box-shadow: inset 0 0 7px #000, .1px .2px 2px 1.1px #023;
}

.projectInfoBlock a:first-child:hover {
  text-shadow: .7px .7px 1px #fff, -.7px -.7px 1px #000, 2px 2px 10px #fff;
  box-shadow: inset 0 0 4px 1px #000, inset 0 6px 7px #008b8b;
}

.projectInfoBlock a:nth-child(2) {
  color: #0ff;
  text-shadow: .5px .5px 1.1px #000, -.9px -.9px 1.1px #f9f9f9;
  border-left: 1px solid #09a565;
  border-right: 1.5px solid #0ff999;
  font-weight: 500;
  box-shadow: inset 0 0 7px #000, .1px .2px 2px 1.1px #023;
}

.projectInfoBlock a:nth-child(2):hover {
  text-shadow: .7px .7px 1px #fff, -.7px -.7px 1px #000, 2px 2px 10px #fff;
  box-shadow: inset 0 0 4px 1px #000, inset 0 6px 7px #008b8b;
}

.inputToggleBox {
  border-radius: 15px;
  width: 100%;
  height: auto;
  display: flex;
}

.wholeFormBox {
  margin: 5%;
  padding: 10px 20px;
}

.wholeFormBox > div {
  justify-content: space-between;
  display: flex;
}

label {
  text-shadow: .5px .5px 1px gray, -.5px -.5px 1px #d3d3d3;
  color: #000;
}

#contact form {
  color: #edd6fe;
  text-shadow: 0 3px 1px #410070;
  background: repeating-radial-gradient(circle at 7% 92%, #333000, #32f 33.18px, #eee 131.179px, purple 143.359px, orange 376.162px, red 625.141px, #fff 688.299px, indigo 803.432px) no-repeat;
  border-radius: 5px;
  max-width: 880px;
  padding: 1rem;
  font-size: 2rem;
  font-weight: 600;
  display: flex;
}

#contact form input {
  text-align: right;
  border: 5px solid #639;
  border-radius: 5px;
  width: 450px;
  margin: 1rem;
  padding: 2rem;
  box-shadow: 0 2px 4px #000;
}

textarea#message {
  color: purple;
  text-shadow: -.5px -.4px 1px #fff, .5px .6px 1px #000;
  background: repeating-radial-gradient(circle at 30% 89%, #333, #333 106.18px, #eee 226.179px, purple 252.359px, orange 271.162px, red 295.141px, #fff 306.299px, indigo 341.432px);
  border-radius: 15px;
  width: 450px;
  margin: 1rem;
  padding: 3rem 2.4rem;
  box-shadow: -2px -2px 1px .24px #fff, 2px 2px 5px .36px #000;
}

.submit-captchaBox button {
  text-shadow: .5px .8px 1px #fff, -.5px -.7px 1px #000;
  color: gray;
  background: #b6b60000;
  border-radius: 8px;
  width: 70%;
  margin: 1rem;
  padding: 1rem 2rem;
  font-size: 3rem;
  box-shadow: inset 0 -4px 4px 1px #000, inset 4px 8px 8px 1px orange, .2px .1px 1px 1px #000, -.3px -.3px 1px 1px #fff;
}

.submit-captchaBox button:hover {
  text-shadow: .5px .5px 1px #fff, -.5px -.7px 1px #000, .1px .1px 10px #ff4500;
  box-shadow: inset 0 4px 4px 1px #000, inset 4px 8px 8px 1px #a9a9a9, .2px .1px 1px 1px #000, -.3px -.3px 1px 1px #fff;
}

